<h2 id="introduction" tabindex="-1"><a class="header-anchor" href="#introduction" aria-hidden="true">#</a> Introduction</h2>
<p>In this course you will learn in more detail how the communication between clients and servers on the web works, i.e. learn the HTTP protocol. This will then be used to create a &quot;web application&quot; not only web browsers can communicate with, but any type of application connected to the Internet, using a REST API. This is convenient for smartphone applications and similar when they need to be able to store data from the users in the cloud instead of locally on the smartphone device.</p>
<FigureNumber /> below displays an overview of how it can work. 
<Figure caption="Common web application architecture.">
<RenderMermaid graph-definition="
graph LR
	users[Users]
	subgraph Users' smartphone
		webBrowser[Android App]
	end
	subgraph Web Server
	webApp[Web Application]
	end
	subgraph Database Server
		db[Database]
	end
	users -- Use --> webBrowser
	webBrowser -- HTTP/REST API --> webApp
	webApp -- SQL --> db
" />
</Figure>
<p>At course start you are expected to possess knowledge corresponding to the pre-requisite courses shown in <FigureNumber /> below.</p>
<Figure caption="Prerequisites for this course.">
<RenderMermaid graph-definition="
graph TD
	wuid[Web and User Interface Design 7.5 Credits]
	gdvc[Graphic Design and Visual Communication 15 Credits]
	csp[Client-side Programming 15 Credits]
	awa[Applied Web Architecture 15 Credits]
	csc[Client-Server Communication 7.5 Credits]
	wuid --> csp
	gdvc --> csp
	csp --> awa
	awa --> csc
" />
</Figure>
