<h3 id="information-to-students" tabindex="-1"><a class="header-anchor" href="#information-to-students" aria-hidden="true">#</a> Information to students</h3>
<p>Information about the <strong>Laboratory Work</strong> can be found on the page <RouterLink to="/courses/client-server-communication/lab-instructions/">Lab Instructions</RouterLink>. It will teach you how to build a Single-Page Application in Vue.js communicating with a backend using a Software Development Kit.</p>
<p>Information about the <strong>Project Work</strong> can be found on the page <RouterLink to="/courses/client-server-communication/project-instructions/">Project Instructions</RouterLink>. It's about creating a platform consisting of a frontend application and a backend application communicating using a REST API.</p>
<p><strong>Written Examination</strong> is a <a href="https://ju.se/student/en/digital-exam.html" target="_blank" rel="noopener noreferrer">Digital Exam<OutboundLink/></a> in Inspera at the end of the course. You need to register for it in advance per the instructions at <a href="https://ju.se/student/en/studies/examination.html" target="_blank" rel="noopener noreferrer">https://ju.se/student/en/studies/examination.html<OutboundLink/></a>. Sample answers are only published for the ordinary exam, not the re-exams. For the re-exams, come and talk with the course coordinator if there are any answers you are unsure of.</p>
<ul>
<li><a href="files/exam-2019-10-15-sample-answers.txt">Sample answers to the exam 2019-10-15</a></li>
<li><a href="files/exam-2020-08-13-sample-answers.txt">Sample answers to the exam 2020-08-13</a></li>
</ul>
<h3 id="conditions-for-the-examined-elements" tabindex="-1"><a class="header-anchor" href="#conditions-for-the-examined-elements" aria-hidden="true">#</a> Conditions for the examined elements</h3>
<p>Conditions for the <strong>Laboratory Work</strong> can be found on the page <RouterLink to="/courses/client-server-communication/lab-instructions/">Lab Instructions</RouterLink>.</p>
<p>Conditions for the <strong>Project Work</strong> can be found on the page <RouterLink to="/courses/client-server-communication/project-instructions/">Project Instructions</RouterLink>.</p>
<p>No aids allowed are allowed for the <strong>Written Examination</strong>. The computer you write the exam on may only be used to answer the questions on the exam, nothing else.</p>
<h3 id="re-examination-of-this-course" tabindex="-1"><a class="header-anchor" href="#re-examination-of-this-course" aria-hidden="true">#</a> Re-examination of this course</h3>
<p>Re-examination for the <strong>Laboratory Work</strong> is offered in the re-exam periods in January and August. If you need to know a precise date and time in advance, contact the course coordinator.</p>
<p>Re-examination for the <strong>Project Work</strong> is offered in the re-exam periods in January and August. If you need to know a precise date and time in advance, contact the course coordinator. The project work will only be graded 3/U (U=fail) at re-examination.</p>
<p>Re-examination for the <strong>Written Examination</strong> is offered in the re-exam periods in January and August.</p>
<p>If you haven't passed some examination tests after the three examination opportunities (ordinary + 2 re-exam) you need to re-take those tests the next time the course run. Note that the instructions for those tests might have changed the next time the course run.</p>
<h2 id="grading-criteria" tabindex="-1"><a class="header-anchor" href="#grading-criteria" aria-hidden="true">#</a> Grading criteria</h2>
<p>To receive a final grade on the course you need to pass all examination tests (Laboratory Work + Project Work + Written Examination). The final grade will be based on the lowest grade on the Project work and the Written Examination, but these grades will also be marked as &quot;weak&quot; or &quot;strong&quot;. If one of the grades is lower than the other and has been marked as &quot;strong&quot;, the final grade will be the lower grade + 1. For example, a strong 3 and a weak 4 gives you 4 as the final grade, while a weak 3 and a strong 5 gives you 3 as the final grade. For the Written Examination, 50% is a strong 3, and 70% is a strong 4.</p>
